<script>
import Vue from "vue"
import Component from "vue-class-component"
import images from 'COMPONENTS/layout/images.vue'
import axios from "axios"
import localstorage from '@/utils/localstorage'
@Component({
  components: {
    images
  }
})
export default class PageOptimal extends Vue {
  // cdn 图片路径
  cdnPath = `${process.env.VUE_APP_CDNPATH}/website/`
  vkey = null
  hasOpen = false
  api_root = process.env.VUE_APP_ENV === 'development'
        ? 'https://zhaopin-api.lieduoduo.ziwork.com'
        : 'https://zhaopin-api.lieduoduo.com'
  baidu() {
    var head = document.getElementsByTagName('head')[0]
    var script = document.createElement('script')
    script.src = "https://hm.baidu.com/hm.js?fe10327641b63e67d706190cfb16c041"
    head.appendChild(script)
  }
  /**
   * @Author   小书包
   * @DateTime 2019-03-04
   * @detail   添加网站的关键词和描述
   * @return   {[type]}   [description]
   */
  appendHead() {
    var head = document.getElementsByTagName('head')[0]
    // 添加关键词
    var meta_keywords = document.createElement('meta')
    meta_keywords.name = 'keywords'
    meta_keywords.content = '猎多多，找工作，求职，招聘，精英人才，互联网精英，约面，社交，小程序找工作，面试，高端招聘，跳槽，换工作，互联网招聘，微信招聘，高薪职位，互联网行业，职场招聘， 猎头招聘，校招，社招，全职，兼职，实习'
    head.appendChild(meta_keywords)

    var meta_description = document.createElement('meta')
    meta_description.name = 'Description'
    meta_description.content = '猎多多以“人”为核心，通过社交约面的方式，让候选人和老板直接面谈，为中高端候选人提供更高效的求职面试服务，为招聘企业提供更多优质互联网精英人才。'
    head.appendChild(meta_description)
  }
  submit() {
    axios.put(`${this.api_root}/advisor/selection/apply/${this.vkey}`, {
      headers: {
        'Authorization': localstorage.get('token')
        // 'Wechat-Version': localstorage.get('version')
      }
    })
    .then(res => {
      this.$toast('申请已提交，顾问将尽快与你联系')
    })
    .catch(err => {
      let infos = err.response.data
      this.$toast(infos.msg)
    })
  }
  mounted() {
    let query = this.$route.query
    this.appendHead()
    localstorage.set('sessionToken', query.sessionToken)
    localstorage.set('token', query.token)
    localstorage.set('version', query.version)
    this.vkey = query.vkey
    this.hasOpen = !!Number(query.iso)
    // document.body.innerHTML = JSON.stringify(this.$route.query)
  }
}
</script>
